	module.exports = {"data":{"markdownRemark":{"html":"<p>まずは<a href=\"http://dqname.jp/\">まとめサイト</a>参照のこと。 最近DQNなクソガキが増えてると思ったら親もDQNなのね。こういう名を付けてる親の気が知れない。己の子供を生きた玩具かあるいはペットとしてしか思ってないのだろうか。新生児が拒絶出来るわけがないので、事実上強制的に名付けられた子供の将来を考えると不憫に思えてくる。 よく「名は体を現す」と言うが、自分は名前も体の一部だと思っている。 子供は(少なくとも純粋な心の残っている年齢のうちは)親などから名前の真意を知ると、その様に生きて行きたいと自発的に思うものだ。 しかし、それにノリでふざけたり必要以上に珍妙な名前を付けたりすることは、子供を故意に奇形児にさせているに等しいのではないのだろうか。言い過ぎかもしれないが、身体障害者や精神障害者ならぬ名前障害者だ。変な名前を与えれば幼少期はそれだけでいじめられることだってあるだろう。それだけでも十分幼い心に深い傷を負うだろうし、まして大きくなってある程度頭の出来てきた頃に名前の真意を初めて理解して落ち込み、その後を想像するだけでまだ長いであろう後生が嫌になることだってあるだろう。 近い将来名前がヘンなせいで自殺、なんて事件も多発するんじゃなかろうか。この子供たちが日本を背負っていく数十年後がどうなるか心配だ。</p>","frontmatter":{"cover":null,"date":"2006-01-28T00:05:00+09:00","strDate":"2006/1/27","title":"DQName(子供の名前について一考)"}}},"pathContext":{"path":"/ja/blog/2006/01/28-dqname子供の名前について一考/","langKey":"ja"}}

